___CSS_LOADER_EXPORT___.push([module.id, `.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  padding: 20px;
  background-color: #f8f9fa;
  font-family: 'Open Sans', sans-serif;
  color: #333;
}

.header {
  margin-bottom: 20px;
}

.header h1 {
  font-weight: 600;
  font-size: 2.5em;
  color: #333;
}

.main {
  flex-grow: 1;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.main h2, .main h3 {
  font-weight: 600;
  font-size: 1.5em;
  color: #666;
  margin-bottom: 10px;
}

.main p {
  font-weight: 300;
  font-size: 1em;
  line-height: 1.5;
  color: #333;
  margin-bottom: 20px;
}

.main button, .main input, .main textarea {
  width: 100%;
  padding: 10px;
  font-size: 1em;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-bottom: 20px;
}

.main button {
  color: #fff;
  background-color: #4caf50;
  cursor: pointer;
  transition: background-color 0.2s ease, transform 0.2s ease;
}

.main button:hover {
  background-color: #4caf50;
  transform: scale(1.05);
}

.footer {
  margin-top: 20px;
}

.footer p {
  font-weight: 300;
  font-size: 0.8em;
  color: #666;
}
